body {

}
.im{
    background-image: url('https://media.giphy.com/media/VI2UC13hwWin1MIfmi/giphy.gif');
    background-size: cover;
    height: 100vh;
    padding:50px;
    margin:0;
}
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}


video {
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
  }


.center {
    margin: auto;
    width: 50%;
    border: 3px solid green;
    padding: 10px;
  }

.box {
    width: 65%;
    height: 400px;
    padding: 4%;
    position: absolute;
    top: 100px;
    left: 67%;
    background: black;
    text-align: center;
    transition: 0.25s;
    margin-top: 10%;
    border: 3px;
    border-radius: 7%;
    
}

.box input[type="text"],
.box input[type="password"] {
    border: 0;
    background: none;
    background-color: none;
    display: block;
    margin: 5% auto;
    text-align: center;
    border: 2px solid #3498db;
    padding: 3%;
    width: 65%;
    outline: none;
    color: rgb(255, 255, 255);
    border-radius: 24px;
    transition: 0.25s
}

.box h1 {
    color: white;
    text-transform: uppercase;
    font-weight: 500
}

.box input[type="text"]:focus,
.box input[type="password"]:focus {
    width: 73%;
    border-color: #2ecc71;
    color: white;
}

.box input[type="submit"] {
    border: 0;
    background-color: none;
    background: none;
    background-color: none;
    display: block;
    margin: auto;
    text-align: center;
    border: 3% solid #2ecc71;
    padding: 3% 4%;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.25s;
    cursor: pointer
}

.box input[type="submit"]:hover {
    background: #2ecc71
}